@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
header{display:flex;position:fixed;top:0;left:0;width:100%}#navbar{font-size:1.4em;width:100%;text-align:right;display:flex;justify-content:flex-end;background-color:#fe652a}.nav-link{padding:10px;text-decoration:none;color:black}.nav-link:hover,.nav-link:focus{color:#fe652a;background-color:#414042}main{padding-top:20px}#welcome-section{display:flex;align-items:center;justify-content:center;flex-direction:row;width:100%;height:100vh;color:#fe652a;background-color:#414042}#skills_container{border-style:dashed;border-color:#fe652a;padding:20px}#projects{display:flex;flex-direction:column;align-items:center;justify-content:center}#projects_title{padding-top:10vh;color:#fe652a}#projects_container{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr;grid-column-gap:30px;grid-row-gap:15px;width:80%;padding-bottom:30px}a{text-decoration:none}.project-tile{text-decoration:none;margin-bottom:auto}.project-tile:hover .project_name{color:#414042;background-color:#fe652a}.project_screenshot{width:100%;vertical-align:top;border-radius:10px 10px 0px 0px}.project_name{font-size:1.5em;padding:20px;margin-top:0px;border-radius:0px 0px 10px 10px;background-color:#414042;color:#fe652a}#contact{padding-top:20px;background-color:#fe652a;color:black}#profile_links_flexbox{height:100px;display:flex;justify-content:center}.profile_links{margin:20px 5px 20px 5px}#linkedin_logo,#github_logo_mark,#twitter_logo,#email_logo{width:32px;transition:width 250ms}#linkedin_logo:hover,#linkedin_logo:focus,#github_logo_mark:hover,#github_logo_mark:focus,#twitter_logo:hover,#twitter_logo:focus,#email_logo:hover,#email_logo:focus{width:48px}#freecodecamp_logo{height:32px;transition:height 250ms}#freecodecamp_logo:hover,#freecodecamp_logo:focus{height:48px}footer{text-align:right;padding:0 20px 0 20px;background-color:#414042;color:#fe652a}@media (max-width: 1250px){h1{font-size:2.5em}h2,#navbar{font-size:1.8em}}@media (max-width: 950px){h1{font-size:2em}h2,#navbar{font-size:1.5em}#skills_container,.project_name,footer{font-size:1em}#skills_container{text-align:left}}@media (max-width: 750px){h1{font-size:1.2em}h2,#navbar{font-size:1em}#skills_container,.project_name,footer{font-size:0.8em}}@media (max-width: 550px){#projects_container{display:flex;flex-direction:column}}body{font-family:"Montserrat",sans-serif;text-align:center;background-color:#58595b}h1{font-size:3em}h2{font-size:2em}

